import { AuthContext } from 'components/AuthProvider';

import { useCallback, useContext, useMemo } from "react";
import apiAuth from 'common/api/apiAuth';
import { toast } from 'react-toastify';
import { LoginResult } from 'common/types';
import lsHelper from 'common/lsHelper';
import appConst from 'common/appConst';
import util from 'common/util';
import { setAuthorizationHeader } from 'common/axios';

export default function useAuth() {
  const { loginResult, setLoginResult } = useContext(
    AuthContext
  );

  const token = useMemo(() => {
    return loginResult?.token;
  }, [loginResult]);

  const isAuthenticated = !util.isNullOrEmpty(loginResult?.token);

  const handleLogin = useCallback((result: LoginResult) => {
    lsHelper.set(appConst.key.loginResult, result);
    //setAuthorizationHeader(result.token);
    //setLoginResult(result);
    //window.location.href = `${process.env.REACT_APP_HOST}/#/${appConst.landingPage}`;

    (function redirectIfLoginResultAlreadySaved(){
      const lrFromLs = lsHelper.get<LoginResult>(appConst.key.loginResult);

      if (lrFromLs != null) {
        console.log('lrFromLs is', lrFromLs);
        window.location.href = `${process.env.REACT_APP_HOST}/#/${appConst.landingPage}`;
        window.location.reload();
      } else {
        console.log('FAIL', lrFromLs);
         setTimeout(redirectIfLoginResultAlreadySaved, 30);
      }
    })();
  }, []);

  const loginAsGuest = useCallback((onFinish: () => void) => {
    apiAuth.loginAsGuest()
      .then(res => {
        if(res.success){
          //setAuthorizationHeader(res.result!.token);
          handleLogin(res.result!);
        }
        else{
          toast.error(res.message);
        }
      })
      .catch()
      .finally(() => onFinish());
  }, []);

  const loginAsGoogleAccount = useCallback((idToken: string, guestId: string | null, onFinish: () => void) => {
    apiAuth.loginAsGoogleAccount(idToken, guestId)
      .then(res => {
        if(res.success)
          handleLogin(res.result!)
        else
          toast.error(res.message);
      })
      .catch()
      .finally(() => onFinish());
  },[]);

  const logout = useCallback(async (onFinish: () => void) => {
    lsHelper.remove(appConst.key.loginResult);
    window.location.href = `${process.env.REACT_APP_HOST}`;
    onFinish();
  }, []);

  return {
    data: {
      isAuthenticated,
      loginResult,
      token,
    },
    methods: {
      logout,
      loginAsGuest,
      loginAsGoogleAccount,
    },
  };
}